.App{
  text-align: center;
  font-family: Arial, sans-serif;
}

.App-header{
    background-color: #282c34;
    padding: 20px;
    color: azure;
}

nav ul{
list-style: none;
padding: 0;
}

nav ul li{
  display: inline;
  margin: 0 10px;
}

nav ul li a {
  color: white;
  text-decoration: none;
}

section {
  margin: 20px;
}

section h2 {
  border-bottom: 1px solid #ccc;
  padding-bottom: 5px;
}

section p, section div {
  text-align: left;
}

section div {
  margin: 10px 0;
}